						<span class="order-item-title">持卡人：</span><span>{{orderInfo.repayCardUser | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">卡号：</span><span>{{orderInfo.repayCardNo | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">开户行：</span><span>{{orderInfo.repayCardBank | empty}}{{orderInfo.repayCardBranck | empty}}</span>
					</div>
				</div>
			</div>
			<div class="order-cell">
				<div class="cell-title">人行征信摘要</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">查询次数：</span><span>{{orderInfo.xxx | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">历史逾期情况：</span><span>{{orderInfo.xxx | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">当期逾期情况：</span><span>{{orderInfo.xxx | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">当期逾期金额：</span><span>{{orderInfo.xxx | empty}}</span>
					</div>
				</div>
			</div>
			<div class="order-cell">
				<div class="cell-title">审核历史</div>
				<div class="end-body">
