						<Panel :name="'panel'+index" v-for="(item,index) in orderInfo.logViewList" :key="item.auditingTime">
							{{item.nodeName}}&nbsp;&nbsp;{{item.status | empty | fstatus}}
							<div slot="content" class="history-cell" >
								{{item.accountName | empty}}&nbsp;&nbsp;
								{{item.userName | empty}}&nbsp;&nbsp;
								{{item.auditingTime | empty | fDate('yyyy-MM-dd hh:mm:ss')}}&nbsp;&nbsp;
								【{{item.nodeName | empty}}】&nbsp;&nbsp;
								{{item.status | empty | fstatus}}&nbsp;&nbsp;
								备注说明：{{ item.conent | empty }}
								<img-list :imgList="item.accessoryImg"></img-list>
							</div>
						</Panel>
					</Collapse>
				</div>
			</div>
			<div class="order-cell" v-if="$route.params.tab == 'history'">
				<div class="cell-title">实际放款信息</div>
				<div class="cell-body" v-if="orderInfo.grantMoney">
					<div class="cell-item">
						<span class="order-item-title">实际放款金额：</span><span>{{orderInfo.grantMoney.realLoanMoney | fmoney | empty }}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">放款模式：</span><span>{{orderInfo.grantMoney.loanMethod==0?'T+0':'T+1' | empty}}</span>
					</div>
					<div class="cell-item">
						<img-list name="放款凭证" :imgList="orderInfo.grantMoney.loanImage"></img-list>
					</div>
				</div>
			</div>
			<div class="order-cell cell-end" v-if="$route.params.tab == 'pending'">
				<div class="loan-title">资料核对</div>
				<div class="beizhu">
					<span class="order-item-title">备注：</span>
					<Input class="text-box" type="textarea" v-model="outRemark" :maxlength="300" :rows="4"
						   placeholder="300字以内..."></Input>
				</div>
				<up-load
					v-if="orderInfo.scratchFileList"
					:imgList="orderInfo.scratchFileList"
					:actionUrl="actionUrl"
					:upData="upData"
					@upLoad="handleUpLoad"></up-load>
				<div class="submit-box">
					<i-button type="info" size="large" class="sub-btn" :disabled="submitNow" @click="submit('0')">
						通过
					</i-button>
					<i-button type="error" size="large" class="sub-btn" :disabled="submitNow" @click="submit('2')">
						不通过
					</i-button>
					<i-button type="ghost" size="large" class="cancel-btn" @click="pageBack">取消</i-button>
				</div>
			</div>
