		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" :disabled="refreshFlag"
				  v-if="$route.params.tab == 'pending'"
				  @click="loadInfo('new')">
			重新获取
		</i-button>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="pageBack">返回</i-button>
	</Tabs>
</template>
<script>
	import DragSwiper from '@/components/dragswiper.vue'
	import AnNex from '@/components/annex.vue'
	import upLoad from '@/components/upload.vue'
	import imgList from '@/components/imglist.vue'
	export default {
		data () {
			return {
				showSwiper: false,
				uploadOpend:false,
				swiperList: [],
				searchParams: {
					loanId:''
				},
				step:'director',
				submitNow: true,
				refreshFlag: false,
				orderInfo: {},
				outRemark:'',
				upData:{
					aBaseName:'',
					loanId:'',
					serialNo:'',
					eProduct:-1
				},
				xwupData:{
					aBaseName:'',
					loanId:'',
					serialNo:'',
					eProduct:''
				},
				upTitle:'补传资料',
				actionUrl:'/imagesDo/uploadImages',
				xwImgList:[],
				xwUpList:[],
				upList:[],
				logList:[]
			}
		},
		components: {
			DragSwiper,AnNex,upLoad,imgList
		},
		computed:{
			xwValue(){
				let list=this.orderInfo.mortgageHouse;
				let values=_.map(list,function (item) {
					return item.nbComfirmValue
				});
				let result=_.reduce(values,function (a,b) {
					a=parseFloat(a);
					b=parseFloat(b);
					if(!_.isFinite(a)){
						a=0
					}
					if(!_.isFinite(b)){
						b=0
					}
					return a+b
				},0);
				return result
			}
		},
		created: function () {
			this.loadInfo();
		},
		mounted(){
			let activeName=this.$route.params.name;
			let nodeStatus=this.$route.params.nodeStatus;
			if(activeName){
				this.$store.dispatch('changeActiveName',activeName);
			}
		},
		methods: {
			loadInfo: function (getNew) {
				let _this = this;
				let params = _this.$route.params;
				_this.refreshFlag = true;
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('/ejie/getDetail', {
					loanId: getNew ? 0 : params.loanId,
					serialNo: params.serialNo
				})
					.then(function (response) {
						clearTimeout(timer);
						_this.$Message.destroy();
						let res = response.data;
						_this.refreshFlag = false;
						_this.submitNow = false;
						if (res.code == '1') {
							_this.orderInfo = res.data;
							_this.$Message.success({
								content: res.msg
							});
							_this.upData.loanId=res.data.loanId;
							_this.upData.serialNo=res.data.serialNo;
							_this.upData.aBaseName=res.data.fileRPath.loan;
							_this.xwupData.aBaseName=res.data.fileRPath.xw;
							_this.xwupData.serialNo=res.data.serialNo;
							_this.xwupData.loanId=res.data.loanId;
							_this.$nextTick(function () {
								_this.$nextTick(function () {
									let index=parseFloat(res.data.logViewList.length)-1;
									_this.logList=['panel'+index];
								})
							})
						} else {
							_this.$Notice.error({
								title: res.msg,
								duration: 3
							});
							_this.pageBack();
						}
					})
					.catch(function (error) {
						//处理错误
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.refreshFlag = false;
						_this.submitNow = false;
						_this.$Notice.error({
							title: error,
							duration: 3
						});
						_this.pageBack();
					});
			},
			handleUpLoad:function(list){
				this.upList=list;
			},
			handleXwUpLoad:function (list) {
				this.xwUpList=list;
			},
			submit: function (type) {
				let _this = this;
				let params = _this.$route.params;
				_this.submitNow = true;
				_this.$Modal.confirm({
					title: '审核结果确认',
					content: '是否确认？',
					loading: true,
					onOk: function () {
						let nodeStatus;
						if(type == 0){//通过
							if(params.nodeStatus == 1080){//授信预审，通过则为1001
								nodeStatus=1001;
							}else if(params.nodeStatus == 1081){//放款预审,通过则为1003
								nodeStatus=1003;
							}else {//其余则回到原本节点
								nodeStatus=params.nodeStatus;
							}
						}else {//拒绝
							nodeStatus=params.nodeStatus;
						}
						let submitParams={
							loanId: _this.orderInfo.loanId,
							serialNo:_this.orderInfo.serialNo,
							nodeStatus:nodeStatus,
							outRemark:_this.outRemark,
							loanVoucher:JSON.stringify(_this.upList),
							flowStatus: type
						};
						_this.$http.post('loanManage/alterAuditAffirm',submitParams)
							.then(function (response) {
								let res = response.data;
								if (res.code == '1') {
									_this.$Modal.remove();
									_this.submitNow = false;
									_this.$Message.success({
										content: res.msg,
										duration: 3
									});
									_this.pageBack();
								} else {
									_this.$Modal.remove();
									_this.$Message.error({
										content: res.msg,
										duration: 3,
										onClose: function () {
											_this.submitNow = false;
											_this.pageBack();
										}
									});
								}
							})
							.catch(function (error) {
								_this.$Modal.remove();
								_this.submitNow = false;
								_this.$Message.error({
									content: error,
									duration: 3
								});
							})
					},
					onCancel: function () {
						_this.submitNow = false;
					}
				})
			},
			showCard: function (params) {
				let _this = this;
				_this.searchParams.loanId = _this.orderInfo.serialNo;
				_.assign(_this.searchParams,params);//混入搜索条件
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '图片请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('loanManage/getFilePath', _this.searchParams)
					.then(function (response) {
						var res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == 1) {//正常
							_this.$Message.success({
								content: res.msg
							});
							_this.swiperList = res.data;
							_this.showSwiper = true;
						} else {
							_this.$Message.error({
								content: res.msg
							});
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Message.error({
							content: error
						});
					});
			},
			showUpload:function (params) {
				this.xwupData.eProduct=params.thirdType;
				this.upTitle=params.thirdTypeName;
				this.loadDefaultImg(params.thirdType)
			},
			loadDefaultImg:function (name) {
				let _this=this;
				this.$http.post('/imagesDo/listSelectScratchFile',{
					serialNo:_this.orderInfo.serialNo,
					eProduct:name
				})
					.then(function (response) {
						let res=response.data;
						if(res.code == 1){
							_this.$Message.success(res.msg);
							_this.xwImgList=res.data;
							_this.uploadOpend=true;
						}else {
							_this.$Message.warning(res.msg);
						}
					})
			},
			uploadXwImg:function () {
				let _this=this;
				this.$http.post('/imagesDo/trueUploand',{
					loanId:_this.orderInfo.loanId,
					serialNo:_this.orderInfo.serialNo,
					loanVoucher:JSON.stringify(_this.xwUpList),
					eProduct:_this.xwupData.eProduct
				})
					.then(function (response) {
						let res=response.data;
						if(res.code == 1){
							_this.$Message.success(res.msg);
						}else {
							_this.$Message.warning(res.msg);
						}
					})
			},
			pageBack: function () {//返回，取消
				let _this = this;
				let params = _this.$route.params;
				if (params.tab) {
					_this.$router.push({name: params.name, params: {tab: params.tab}})
				} else {
					_this.$router.go(-1);
				}
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}
	.tab-item{
		padding-bottom: 100px;
	}
	.ivu-tabs-tab {
		font-size: 16px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}
	.order-title {
		display: flex;
		justify-content: space-between;
		color: #343434;
		font-size: 14px;
		padding-bottom: 16px;
	}

	.order-cell {
		padding-bottom: 5px;
		border-bottom: 1px solid #e5e5e5;
		padding-top: 30px;
	}

	.cell-title {
		height: 14px;
		border-left: 3px solid #ff8226;
		color: #ff8226;
		font-size: 14px;
		line-height: 14px;
		padding-left: 10px;
		margin-bottom: 30px;
	}

	.cell-body {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
	}

	.cell-item {
		min-width: 300px;
		padding-bottom: 15px;
		padding-right: 20px;
		color: #333333;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		font-size: 14px;
	}
	.history-cell{
		display: block;
		min-width: 300px;
		color: #333333;
		font-size: 14px;
	}
	.order-item-title {
		width: 100px;
		height: 22px;
		line-height: 22px;
		color: #7f7f7f;
		overflow: hidden;
	}
	.labelColor .ivu-form-item-label {
		color: #7f7f7f
	}
	.loan-title {
		width: 110px;
		height: 34px;
		line-height: 34px;
		background-color: #20abff;
		text-align: center;
		color: #fff;
		font-size: 14px;
		margin-bottom: 30px;
	}

	.beizhu {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding-bottom: 15px;
	}
	.beizhu .text-box{
		width: 600px;
	}
	.submit-box {
		text-align: left;
		padding-top: 30px;
		padding-bottom: 30px;
	}
	.sub-btn {
		width: 120px;
		margin-right: 10px;
	}

	.cancel-btn {
		width: 120px;
		color: #2d8cf0;
		border: 1px solid #2d8cf0;
	}
</style>



